import React, { useImperativeHandle, useState, forwardRef } from "react";
import { Mobile, PC } from "../Info/MediaQuery";

const PlitviceMotion = forwardRef((props, ref) => {
  const [transform, setTransform] = useState(false);
  let dynamicSrc = "img/motion/일반.gif";
  let dynamicTransForm = -1;

  useImperativeHandle(ref, () => ({
    AnimateDance: () => {
      props.setPlitviceAni(true);
      setTimeout(() => setTransform(true), 1200);
      setTimeout(() => setTransform(false), 2200);
      setTimeout(() => setTransform(true), 3200);
      setTimeout(() => setTransform(false), 4200);
      setTimeout(() => props.setPlitviceAni(false), 4200);
    },
  }));

  if (props.plitviceAni) {
    dynamicSrc = "img/motion/댄스.gif";
  } else {
    dynamicSrc = "img/motion/일반.gif";
  }

  if (transform) {
    dynamicTransForm = 1;
  } else {
    dynamicTransForm = -1;
  }

  return (
    <>
      <PC>
        <img
          alt="플비"
          style={{
            position: "absolute",
            left: "1200px",
            top: "450px",
            transform: `scaleX(${dynamicTransForm})`,
          }}
          width="100"
          height="100"
          src={dynamicSrc}
        />
      </PC>
      <Mobile>
        <img
          alt="플비"
          style={{
            position: "absolute",
            top: "340px",
            left: "280px",
            transform: `scaleX(${dynamicTransForm})`,
          }}
          width="50"
          height="50"
          src={dynamicSrc}
        />
      </Mobile>
    </>
  );
});

export default PlitviceMotion;
